import { createContext, useContext, useState, useEffect, ReactNode } from 'react';

export type OnboardingStep =
    | 'EMAIL_VERIFIED'
    | 'PHONE_PENDING'
    | 'PHONE_VERIFIED'
    | null;

export interface User {
    id: string;
    name: string;
    email: string;
    is_onboarded: boolean;
    ui_preferences?: {
        theme?: 'light' | 'dark';
    };
}

interface AuthContextType {
    is_authenticated: boolean;
    user: User | null;
    onboarding_step: OnboardingStep;
    customer_id: string | null;
    sign_in: (user_data?: User) => void;
    sign_out: () => void;
    update_theme: (theme: 'light' | 'dark') => Promise<void>; // Added to interface
    set_onboarding_step: (step: OnboardingStep) => void;
    set_customer_id: (id: string | null) => void;
    get_route_for_step: (step: OnboardingStep) => string;
}

const AuthContext = createContext<AuthContextType | undefined>(undefined);

const STEP_KEY = 'cc_onboarding_step';
const ID_KEY = 'cc_customer_id';
const THEME_KEY = 'cc_theme';

export function AuthProvider({ children }: { children: ReactNode }) {
    const [is_authenticated, set_is_authenticated] = useState(false);
    const [user, set_user] = useState<User | null>(null);
    const [onboarding_step, set_onboarding_step_state] = useState<OnboardingStep>(null);
    const [customer_id, set_customer_id_state] = useState<string | null>(null);

    useEffect(() => {
        const saved_step = localStorage.getItem(STEP_KEY);
        if (saved_step && saved_step !== 'null') set_onboarding_step_state(saved_step as OnboardingStep);

        const saved_id = localStorage.getItem(ID_KEY);
        if (saved_id && saved_id !== 'null') set_customer_id_state(saved_id);

        const saved_theme = localStorage.getItem(THEME_KEY) || 'light';
        document.body.classList.add(`theme-${saved_theme}`);
    }, []);

    const set_onboarding_step = (step: OnboardingStep) => {
        set_onboarding_step_state(step);
        if (step === null) localStorage.removeItem(STEP_KEY);
        else localStorage.setItem(STEP_KEY, step);
    };

    const set_customer_id = (id: string | null) => {
        set_customer_id_state(id);
        if (id === null) localStorage.removeItem(ID_KEY);
        else localStorage.setItem(ID_KEY, id);
    };

    const get_route_for_step = (step: OnboardingStep): string => {
        switch (step) {
            case 'EMAIL_VERIFIED':
            case 'PHONE_PENDING':
                return '/customer/verify-mobile';
            case 'PHONE_VERIFIED':
                return '/customer/home';
            default:
                return '/customer/signin';
        }
    };

    const update_theme = async (theme: 'light' | 'dark') => {
        document.body.classList.remove('theme-light', 'theme-dark');
        document.body.classList.add(`theme-${theme}`);
        localStorage.setItem(THEME_KEY, theme);

        if (user) {
            set_user({
                ...user,
                ui_preferences: { ...user.ui_preferences, theme }
            });
        }

        if (is_authenticated) {
            try {
                await fetch('http://localhost:5000/api/customers/ui-preferences', {
                    method: 'PATCH',
                    headers: { 'Content-Type': 'application/json' },
                    body: JSON.stringify({ theme }),
                    credentials: 'include'
                });
            } catch (err) {
                console.error('Failed to sync theme', err);
            }
        }
    };

    const sign_in = (user_data?: User) => {
        set_is_authenticated(true);
        if (user_data) {
            set_user(user_data);
            if (user_data.ui_preferences?.theme) {
                update_theme(user_data.ui_preferences.theme);
            }
        }
        set_onboarding_step(null);
    };

    const sign_out = () => {
        set_is_authenticated(false);
        set_user(null);
        set_onboarding_step(null);
        set_customer_id(null);
        localStorage.clear();
        sessionStorage.clear();
        window.location.href = '/customer/signin';
    };

    const value = {
        is_authenticated,
        user,
        onboarding_step,
        customer_id,
        sign_in,
        sign_out,
        update_theme,
        set_onboarding_step,
        set_customer_id,
        get_route_for_step
    };

    return <AuthContext.Provider value={value}>{children}</AuthContext.Provider>;
}

export const useAuth = () => {
    const context = useContext(AuthContext);
    if (!context) throw new Error('useAuth must be used within AuthProvider');
    return context;
};