import { useState, useEffect } from 'react';
import { useRouter } from 'next/router';
import CustomerLayout from '../../components/customer/CustomerLayout';
import { useAuth } from '../../context/AuthContext';
import { useToast } from '../../components/ui/ToastProvider';

// Define Address Interface
interface Address {
    _id: string;
    flatNo?: string; // Mapped from UI inputs if needed, but backend schema has line1, etc.
    // Let's align with Backend Schema:
    line1: string;
    line2?: string;
    area: string;
    city: string;
    state: string;
    pincode: string;
    label?: string;
    is_primary: boolean;
}

export default function AddressPage() {
    const router = useRouter();
    const { isAuthenticated } = useAuth(); // Could use token for fetch
    const [addresses, setAddresses] = useState<Address[]>([]);
    const [activeTab, setActiveTab] = useState<'main' | 'second'>('main');
    const [loading, setLoading] = useState(true);
    const { showToast } = useToast();

    useEffect(() => {
        fetchAddresses();
    }, []);

    const fetchAddresses = async () => {
        try {
            const res = await fetch('http://localhost:5000/api/customers/addresses', {
                headers: { 'Authorization': `Bearer ${/* Handled by cookie usually, or context token if available */ ''}` },
                credentials: 'include'
            });
            if (res.ok) {
                const data = await res.json();
                setAddresses(data);
                // Set initial tab based on data? Main is always primary.
            } else {
                // Handle error
            }
        } catch (error) {
            console.error(error);
        } finally {
            setLoading(false);
        }
    };

    const handleDelete = async (addrId: string) => {
        if (!confirm('Are you sure you want to delete this address?')) return;
        try {
            const res = await fetch(`http://localhost:5000/api/customers/addresses/${addrId}`, {
                method: 'DELETE',
                credentials: 'include'
            });
            if (res.ok) {
                showToast('Address deleted', 'success');
                fetchAddresses();
                setActiveTab('main');
            } else {
                const err = await res.json();
                showToast(err.message || 'Failed to delete', 'error');
            }
        } catch (error) {
            showToast('Server error', 'error');
        }
    };

    const primaryAddress = addresses.find(a => a.is_primary);
    const secondaryAddress = addresses.find(a => !a.is_primary);

    // Determine current view data
    const currentAddress = activeTab === 'main' ? primaryAddress : secondaryAddress;

    return (
        <CustomerLayout disablePadding={true}>
            <div className={`address-page-container has-fixed-header`}>
                {/* Header */}
                <header className="address-header fixed-header">
                    <button
                        className="address-back-btn touchable"
                        onClick={() => router.back()}
                    >
                        <img src="/customer/assets/icons/backward.svg" alt="Back" width={24} height={24} />
                    </button>
                    <h1 className="address-title">Address</h1>
                </header>

                {/* Toggle */}
                <div className="address-toggle-container">
                    <button
                        className={`address-toggle-btn touchable ${activeTab === 'main' ? 'active' : ''}`}
                        onClick={() => setActiveTab('main')}
                    >
                        Main
                    </button>
                    {/* Only show Second tab if it exists or maybe just let them click it to see empty state? 
                        Let's show it always for clarity of "Slots"
                    */}
                    <button
                        className={`address-toggle-btn touchable ${activeTab === 'second' ? 'active' : ''}`}
                        onClick={() => setActiveTab('second')}
                    >
                        Second
                    </button>
                </div>

                {/* Address Display */}
                <div className="address-form">
                    {loading ? (
                        <div className="skeleton-container" style={{ display: 'flex', flexDirection: 'column', gap: '16px' }}>
                            <div className="address-input-group">
                                <span className="skeleton skeleton-text" style={{ width: '30%' }}></span>
                                <span className="skeleton skeleton-rect" style={{ height: '48px', borderRadius: '6px' }}></span>
                            </div>
                            <div className="address-input-group">
                                <span className="skeleton skeleton-text" style={{ width: '20%' }}></span>
                                <span className="skeleton skeleton-rect" style={{ height: '48px', borderRadius: '6px' }}></span>
                            </div>
                            <div className="address-input-group">
                                <div className="skeleton skeleton-rect" style={{ height: '48px', borderRadius: '6px' }}></div>
                            </div>
                        </div>
                    ) : currentAddress ? (
                        <>
                            <div className="address-input-group">
                                <div className="address-input-wrapper">
                                    <label className="address-label">Address Line 1</label>
                                    <input type="text" className="address-input" value={currentAddress.line1} readOnly />
                                </div>
                                <img src="/customer/assets/icons/check.svg" alt="Valid" className="address-valid-icon" />
                            </div>
                            <div className="address-input-group">
                                <div className="address-input-wrapper">
                                    <label className="address-label">Area</label>
                                    <input type="text" className="address-input" value={currentAddress.area} readOnly />
                                </div>
                            </div>
                            <div className="address-input-group">
                                <div className="address-input-wrapper">
                                    <label className="address-label">City</label>
                                    <input type="text" className="address-input" value={currentAddress.city} readOnly />
                                </div>
                            </div>
                            <div className="address-input-group">
                                <div className="address-input-wrapper">
                                    <label className="address-label">State</label>
                                    <input type="text" className="address-input" value={currentAddress.state} readOnly />
                                </div>
                            </div>
                            <div className="address-input-group">
                                <div className="address-input-wrapper">
                                    <label className="address-label">Pincode</label>
                                    <input type="text" className="address-input" value={currentAddress.pincode} readOnly />
                                </div>
                            </div>
                        </>
                    ) : (
                        <div className="empty-address-placeholder">
                            <p>No address details found.</p>
                        </div>
                    )}
                </div>

                {/* CTAs */}
                <div className="address-cta-container">
                    {currentAddress ? (
                        <>
                            <button
                                className="address-cta-btn touchable"
                                onClick={() => router.push(`/customer/edit-address?id=${currentAddress._id}`)}
                            >
                                Edit Address
                            </button>
                            {/* Allow deleting ONLY if:
                                1. Not primary address
                                2. More than 1 address exists (min 1 constraint)
                            */}
                            {!currentAddress.is_primary && addresses.length > 1 && (
                                <button
                                    className="address-cta-btn delete-btn touchable"
                                    style={{ backgroundColor: '#ffcccc', color: '#cc0000', marginTop: '10px' }}
                                    onClick={() => handleDelete(currentAddress._id)}
                                >
                                    Delete Address
                                </button>
                            )}
                        </>
                    ) : (
                        /* Show Add button only if less than 2 addresses (max 2 constraint) */
                        addresses.length < 2 ? (
                            <button
                                className="address-cta-btn touchable"
                                onClick={() => router.push('/customer/edit-address')}
                            >
                                Add New Address
                            </button>
                        ) : (
                            <div className="empty-address-placeholder">
                                <p>Maximum 2 addresses allowed</p>
                            </div>
                        )
                    )}
                </div>
            </div>
        </CustomerLayout>
    );
}
